import {Component, DestroyRef, inject, signal, ViewEncapsulation} from '@angular/core';
import {FormControl, FormGroup, FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FlexColumnComponent} from '../../../shared/view/layout/flex-column.component';

import {FlexRowComponent} from '../../../shared/view/layout/flex-row.component';
import {ButtonGroupItem, ButtonsGroupComponent} from '../../controls/form/buttons-group.component';
import {JellyCalculationModel} from './jelly-calculation.model';
import {DecimalPipe} from '@angular/common';
import {ShrinkDirective} from '../../../shared/view/directives/shrink.directive';
import {RangeComponent} from '../../controls/form/range.component';
import {takeUntilDestroyed} from '@angular/core/rxjs-interop';
import {NumberInputComponent} from '../../controls/form/number-input.component';
import {ParseMathDirective} from '../../../shared/view/directives/parse-math.directive';
import {ControlExtraTemplateDirective} from '../../controls/form/control-extra-template.directive';
import {TranslatePipe} from '@ngx-translate/core';
import {PullDirective} from '../../../shared/view/directives/pull.directive';
import {ButtonComponent} from '../../../shared/view/ui/button/button.component';


@Component({
  selector: 'lg-jelly-widget',
  standalone: true,
  template: `
    <lg-flex-column>
      <form [formGroup]="form">
        <lg-flex-column [size]="'medium'">
          <lg-flex-row [bottom]="true" [mobileMode]="true">
            <lg-flex-column formGroupName="from">
              <span>{{ 'jelly.in-recipe-you-have' | translate }}</span>

              <lg-number-input [placeholder]="''"
                               formControlName="amount"
                               lgParseMath>
                <ng-template lgExtraTpl place="after">
                  {{ 'jelly.grams-of' | translate }}
                </ng-template>
              </lg-number-input>

              <lg-range
                [max]="220"
                [min]="120"
                [step]="10"
                [tickInterval]="20"
                formControlName="bloom"
              ></lg-range>

              <lg-buttons-group [items]="typeButtons" formControlName="type"></lg-buttons-group>
            </lg-flex-column>


            <lg-flex-column formGroupName="to">
              @let fromType = form.value?.from?.type;
              @let toType = form.value?.to?.type;
              <lg-flex-row [strictCenter]="true" class="text-center">
                <lg-flex-column [position]="'center'" [size]="'small'" lgShrink>
                  <div>
                    {{ 'jelly.you-need' | translate }}
                  </div>

                  <lg-flex-row [size]="'small'" style="font-size: 2rem">
                    <div>{{ result() | number: '1.0-2' }}</div>
                    @switch (toType) {
                      @case ("powder") {
                        üçö
                      }
                      @case ("leaf") {
                        üçÉ
                      }
                      @case ("mass") {
                        üß´
                      }
                    }
                  </lg-flex-row>

                  <div>
                    {{ 'jelly.grams-of' | translate }}
                  </div>
                </lg-flex-column>
                @if (waterNeeded()) {
                  <lg-flex-column lgShrink [size]="'small'" [position]="'center'">
                    <div>
                      @switch (toType) {
                        @case ("mass") {
                          <div>
                            {{ 'jelly.with' | translate }}
                          </div>
                        }
                      }

                      @switch (toType) {
                        @case ("powder") {
                          <div>
                            {{ 'jelly.and' | translate }}
                          </div>
                        }
                        @case ("leaf") {
                          <div>
                            {{ 'jelly.and' | translate }}
                          </div>
                        }
                      }
                    </div>

                    <lg-flex-row [size]="'small'" style="font-size: 2rem">
                      <div>{{ waterNeeded() | number: '1.0-2' }}</div>
                      üíß
                    </lg-flex-row>

                    <div>
                      {{ 'jelly.ml-of-water' | translate }}
                      @switch (toType) {
                        @case ("mass") {
                          <br>{{ 'jelly.included' | translate }}
                        }
                      }
                    </div>
                  </lg-flex-column>
                }
              </lg-flex-row>

              <lg-range
                [max]="220"
                [min]="120"
                [step]="10"
                [tickInterval]="20"
                formControlName="bloom"
              ></lg-range>

              <lg-buttons-group [items]="typeButtons" formControlName="type"></lg-buttons-group>
            </lg-flex-column>
          </lg-flex-row>

          <div class="text-small" lgPull>
            @let currentRatio = form.value?.ratio;
            <lg-flex-row [size]="'small'">
              <span>{{ 'jelly.proportion-label' | translate }}:</span>
              <lg-button (onClick)="changeRatio(5)"
                         [outlined]="true"
                         [size]="'tiny'"
                         [style]="currentRatio === 5 ? 'primary' : 'secondary-dark'">
                1/5
              </lg-button>
              <lg-button (onClick)="changeRatio(6)"
                         [outlined]="true"
                         [size]="'tiny'"
                         [style]="currentRatio === 6 ? 'primary' : 'secondary-dark'">
                1/6
              </lg-button>
            </lg-flex-row>
          </div>
        </lg-flex-column>
      </form>
    </lg-flex-column>
  `,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    FlexColumnComponent,
    FlexRowComponent,
    ButtonsGroupComponent,
    DecimalPipe,
    ShrinkDirective,
    RangeComponent,
    NumberInputComponent,
    ParseMathDirective,
    ControlExtraTemplateDirective,
    TranslatePipe,
    PullDirective,
    ButtonComponent
  ],
  styles: [`
    :host {

      --control-bg: #fcfcfc;
    }

    lg-eggs-widget {
      display: flex;
    }

    .eggs-widget {
      display: flex;
      flex-direction: column;
    }

    .eggs-widget__eggs {
      display: flex;
      align-items: flex-end;
      gap: 8px;
    }

    .eggs-widget__eggs img {
      width: 40px;
    }

    .eggs-widget__egg {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 1;
      flex-direction: column;
      white-space: nowrap;
      gap: 8px;
      background-color: var(--control-bg);
      border-radius: 24px;
      padding: 16px;
    }

    .eggs-widget__egg:first-child img {
      transform: scale(0.8);
    }

    .eggs-widget__egg:last-child img {
      transform: scale(1.2);
    }

    .eggs-widget__egg.selected {
      background-color: #61b789;
    }

    .eggs-widget__egg.selected:first-child {
      background-color: #b4b8f8;
    }

    .eggs-widget__egg.selected:last-child {
      background-color: #ff8080;
    }
  `
  ],
  encapsulation: ViewEncapsulation.None,
})
export class JellyWidgetComponent {

  constructor() {
    this.form.valueChanges.pipe(
      takeUntilDestroyed(this.destroyRef),
    ).subscribe((value) => {
      const amount = parseFloat(String(value?.from?.amount));
      if (isNaN(amount)) {
        return;
      }
      const model = new JellyCalculationModel(
        value?.from?.type as any,
        value?.from?.bloom as any,
        value?.ratio as any
      );
      const convertedAmount = model.convertToBase(value?.to?.type as any, amount, value?.to?.bloom as any);
      const waterNeeded = model.convertToWater(value?.to?.type as any, convertedAmount);

      this.result.set(convertedAmount);
      this.waterNeeded.set(waterNeeded);
    });
  }

  result = signal<number>(0);
  waterNeeded = signal<number>(0);
  destroyRef = inject(DestroyRef);
  form = new FormGroup({
    from: new FormGroup({
      type: new FormControl('mass'),
      amount: new FormControl(null),
      bloom: new FormControl(140),
    }),
    to: new FormGroup({
      type: new FormControl('powder'),
      bloom: new FormControl(140),
    }),
    ratio: new FormControl(5),
  })

  typeButtons: ButtonGroupItem[] = [
    {
      label: 'jelly.type.powder',
      value: 'powder',
      style: 'secondary'
    },
    {
      label: 'jelly.type.leaf',
      value: 'leaf',
      style: 'secondary'
    },
    {
      label: 'jelly.type.mass',
      value: 'mass',
      style: 'secondary'
    }
  ];

  changeRatio(ratio: number) {
    this.form.get('ratio')?.setValue(ratio);
  }
}
